.trading_bg-img{
    background-image: url("./../../../../assets/images/Businesses/Raichand Trading/trading/about trading.webp");
    background-repeat: no-repeat;
    background-size: cover;
    padding: 20px 40px;
    max-width: 1300px;
    margin: auto;
}
.trading_products_main_container{
    display: flex;
justify-content: space-around;
flex-wrap: wrap;
    column-gap: 40px;
    row-gap: 40px;
    row-gap: 30px;
}
.img_content_container{
    display: flex;
    justify-content: space-around;
}
.trading_content{
    width: 800px;
}
.trading_img{
    width: 100%;
    height: 200px;
    margin: 0 auto;
}
.products_card{
    border: 1px solid black;
    padding: 20px 30px;
    border-radius: 15px;
    box-shadow: 2px 2px 5px black;
    width: 300px;
}
.trading_btn_div {
    display: flex;
    justify-content: center;
    align-items: center;
}
.trading_card_btn {
    border: 2px solid black;
    background-color: white;
    padding: 3px 15px;
    margin-top: 5px;
    border-radius: 20px;
    width: 120px;

}

.trading_card_btn:hover {
    background-color: black;
    border: 2px solid black;
    color: white;
}
@media only screen and (max-width:600px){
    .trading_products_main_container{
        display: grid;
        grid-template-columns: repeat(1,1fr);
        place-items: center;
        /* column-gap: 40px; */
        row-gap: 30px;
    }
    .img_content_container{
        display: flex;
        flex-wrap: wrap;
    }
    .trading_content{
        width: 580px;
        order: 2;
        margin-top: 20px;
    }
    .trading_img{
        height: 300px;
    }
    .trading_img_div{
        order: 1;
        width: 300px;
     
    }
}
@media only screen and (max-width:834px) and (min-width:601px){
    .trading_products_main_container{
        display: grid;
        grid-template-columns: repeat(2,1fr);
        place-items: center;
        /* column-gap: 40px; */
        row-gap: 30px;
    }
    .img_content_container{
        display: flex;
        flex-wrap: wrap;
    }
    .trading_content{
        width: 580px;
        order: 2;
        margin-top: 20px;
    }
    .trading_img_div{
        order: 1;
    }
    
    /* .trading_img{
        width: 150px;
        height: 150px;
    } */
}
@media only screen and (max-width:1024px) and (min-width:835px){
    .trading_products_main_container{
        display: grid;
        grid-template-columns: repeat(2,1fr);
        place-items: center;
        /* column-gap: 40px; */
        row-gap: 30px;
    }
    .trading_content{
        width: 580px;
    }
   
}
@media only screen and (max-width:1280px) and (min-width:1025px){
    .trading_content{
        width: 700px;
    }
}